body {
    background-color: black;
}

.App {
    text-align: center;
    background-color:  black;
    color: white;
    width: 100%;
  }

.nav-link {
    list-style-type: none;
    color: white;
    display: flex;
    flex-direction: column;
}

.navbar {
    color: white;
}

.nav {
    text-align: center;
    padding: 20px;
}

.workouts-table {
    margin-left: auto; 
    margin-right: auto;
    table-layout: fixed;
    padding: 20px;
}
  
.tb-name {
    width: 30%;
}
  
.tb-date {
    width: 30%;
}
  
.tb-view {
    width: 30%;
}

h1 {
    color: #32F5B1;
}

h2 {
    color: #EB46C2;
}

label {
    border: 1px solid white;
    /* border-radius: 25px; */
    margin: 10px;
    padding: 10px;
    font-size: 18px;
}

.user-label {
    border: none;
}

/* input {
    color: #EB46C2;
    margin: 10px;
    height: auto;
} */

input:checked + label {
    background-color: #32F5B1;
    color: #2439D6;
    font-weight: bolder;
}

.selected-checkbox {
    background-color: #32F5B1;
    color: #2439D6;
    font-weight: bolder;
    height: auto;
    margin: 10px;
}

input[type="checkbox"] {
    display: none;
}

input[type="radio"] {
    display: none;
}

button {
	/* box-shadow:inset 0px 1px 0px 0px #2439D6; */
	/* background:linear-gradient(to bottom, #2439D6 5%, #2439D6 100%); */
	background-color: #2439D6;
	border-radius:6px;
	border:1px solid #2439D6;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	/* font-size:15px; */
	font-weight:bold;
	padding:6px 24px;
	text-decoration:none;
	text-shadow:0px 1px 0px #2439D6;
}
button:hover {
    background:linear-gradient(to bottom, #EB46C2 5%, #EB46C2 100%);
	background-color: #2439D6;
}
button:active {
	position:relative;
	top:1px;
}

.small-btn {
    font-size: 16px;
    margin: 10px;
}

.big-btn {
    font-size: 32px;
    margin: 10px;
}

.logout-btn {
    /* display: block;
    width: 100px; */
    text-align: center;
    margin: 0 auto;
}

a {
    color: white;
    text-decoration: none;
}

a:active {
    /* color: #EB46C2; */
    text-decoration: underline;
}

a:hover {
    text-decoration: underline;
    /* color:  #EB46C2; */
}

.workouts-list {
    font-size: 24px;
}

.workouts-list-name {
    font-size: 28px;
    text-decoration: underline;
    color: #32F5B1;
}

.workout-details {
    font-size: 20px;
}

.exercise-reps {
    color: white;
    font-size: 24px;
}

.exercise-title {
    color: #EB46C2;
}

.exercise-desc {
    font-size: 18px;
}

