@media screen and (min-width: 600px) {
    .App {
        display: grid;
        grid-template-columns: 15% 35% 35% 15%;
        grid-template-areas: 
        "header header . ."
        "sidebar main main ."
    }

    .nav-link {
        list-style-type: none;
        color: white;
    }

    .menu {
        text-align: center;
    }
    
    .navbar {
        color: white;
        align-self: start;
    }

    header {
        grid-area: header;
    }

    nav {
        display: grid;
        grid-area: sidebar;
        justify-self: center;
    }

    main {
        display: grid;
        grid-area: main;
        padding: 10px;
    }

    .row {
        display: grid;
        grid-area: main;
    }

    label {
        display: grid;
        align-self: center;
    }
}